html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

body {
  font-family: Helvetica,sans-serif;
}

@font-face {
  body {
    font-family: BEBAS;
    src: url("../fonts/bebas.woff2") format("woff2"), url("../fonts/bebas.woff") format("woff");
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }
}

.container {
  max-width: 940px;
  margin: 0 auto;
}

@media only screen and (max-width: 963px) {
  .container {
    margin: 0 10px;
  }
}

.header__wrap {
  display: flex;
  align-items: baseline;
  padding: 22px 0px 16px 0px;
}

.logo__link {
  text-decoration: none;
  font-size: 0px;
  font-weight: bold;
  font-family: BEBAS,sans-serif;
}

.logo__part1 {
  color: #e8663c;
  font-size: 36px;
}

.logo__part2 {
  color: #f2ae9c;
  font-size: 36px;
}

.header__nav {
  margin-left: auto;
}

@media only screen and (max-width: 670px) {
  .header__nav {
    display: none;
  }
}

.nav {
  font-size: 16px;
  display: inline-block;
  vertical-align: 6px;
  font-family: BEBAS,sans-serif;
}

.nav ul {
  display: flex;
}

.nav a {
  text-decoration: none;
  text-transform: uppercase;
  margin-left: 24px;
  color: #b9b9b9;
  font-weight: bold;
}

.nav a.is-active,
.nav a:hover {
  color: #e8663c;
}

.category-line {
  background-color: #f2ae9c;
  margin-bottom: 24px;
}

.category-line__title {
  color: #ffffff;
  padding: 15px;
  margin: 0px auto;
  background-color: #e8663c;
  border-left: 10px solid #fff;
  border-right: 10px solid #fff;
  text-transform: uppercase;
  font-size: 24px;
  width: 67%;
  font-family: BEBAS,sans-serif;
}

@media only screen and (min-width: 926px) {
  .content {
    display: flex;
  }
}

.main {
  width: 65%;
}

@media only screen and (max-width: 925px) {
  .main {
    width: 83%;
  }
}

@media only screen and (max-width: 600px) {
  .main {
    width: 100%;
  }
}

.aside {
  width: 17%;
  padding-left: 25px;
}

@media only screen and (max-width: 925px) {
  .aside {
    width: 80%;
    margin: 10px auto;
    display: flex;
  }
}

@media only screen and (max-width: 680px) {
  .aside {
    display: block;
  }
}

@media only screen and (max-width: 600px) {
  .aside {
    width: 100%;
    padding: 0 10px;
  }
}

.post {
  display: flex;
}

.post__media {
  margin-bottom: 24px;
}

@media only screen and (max-width: 462px) {
  .post__media {
    margin-bottom: 0px;
  }
}

.post__content {
  padding: 0px 10px;
  width: 74%;
}

.post__content:after {
  content: "";
  display: block;
  height: 14px;
  margin: 50px 0px;
  background: url(../img/particle.png) 0 0;
}

@media only screen and (max-width: 925px) {
  .post__content {
    width: 86%;
  }
}

@media only screen and (max-width: 600px) {
  .post__content {
    width: 100%;
  }
}

.post__description {
  display: flex;
}

@media only screen and (max-width: 462px) {
  .post__description {
    display: block;
  }
}

.post__info {
  width: 23%;
  font-size: 12px;
  color: #b9b9b9;
  font-family: BEBAS,sans-serif;
}

@media only screen and (max-width: 462px) {
  .post__info {
    display: flex;
    width: 100%;
  }
}

.post__text {
  width: 77%;
  font-size: 12px;
  line-height: 1.5;
}

@media only screen and (max-width: 462px) {
  .post__text {
    width: 100%;
  }
}

.post__title {
  margin: 5px 0px 20px 0px;
}

.post__title a {
  font-size: 18px;
  font-family: BEBAS,sans-serif;
  color: #e8663c;
  line-height: 1;
  text-transform: uppercase;
  text-decoration: none;
}

.post__title a:hover {
  color: #f2ae9c;
}

.post__more-link {
  text-decoration: none;
  color: green;
}

.post__more-link:hover {
  color: #e8663c;
}

.post__video {
  height: 300px;
}

.post__share {
  margin-bottom: 70px;
}

.post__link {
  text-decoration: none;
  color: #b9b9b9;
}

.post__link:hover {
  color: #f2ae9c;
}

@media only screen and (max-width: 462px) {
  .date, .tags, .comment {
    width: 33.33%;
    text-align: center;
    margin-bottom: 10px;
  }
}

.icon-col {
  text-align: right;
  width: 25.5%;
}

@media only screen and (max-width: 925px) {
  .icon-col {
    width: 21%;
  }
}

@media only screen and (max-width: 600px) {
  .icon-col {
    display: none;
  }
}

.icon-col__icon {
  height: 75px;
  width: 75px;
  background-color: #e8663c;
  background-position: center center;
  background-repeat: no-repeat;
  display: inline-block;
}

.post-img {
  height: 300px;
  width: 100%;
  object-fit: cover;
}

@media only screen and (max-width: 600px) {
  .post-img {
    height: 200px;
  }
}

.thumb-wrap {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}

.thumb-wrap iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.date__title, .tags__title, .comment__title {
  display: block;
  text-transform: uppercase;
  color: #e8663c;
  font-weight: 600;
  margin: 11px 0px;
}

.date__value, .tags__tag, .comment__value {
  margin-bottom: 11px;
  text-decoration: none;
  color: #b9b9b9;
}

.date__value:hover, .tags__tag:hover, .comment__value:hover {
  color: #f2ae9c;
}

@media only screen and (max-width: 925px) {
  .about, .list, .gallery {
    width: 33.33%;
    margin: 0 5px;
  }
}

@media only screen and (max-width: 680px) {
  .about, .list, .gallery {
    width: 100%;
  }
}

.aside-title {
  color: #e8663c;
  display: flex;
  text-transform: uppercase;
  margin-bottom: 23px;
  font-size: 14px;
  font-family: BEBAS,sans-serif;
}

.aside-title:after {
  content: "";
  flex: 1;
  margin-left: 10px;
  background: url(../img/particle.png) 0 0;
}

.about__text {
  margin-bottom: 37px;
  font-size: 12px;
  line-height: 1.2;
}

.list__nav {
  margin-bottom: 30px;
}

.list__nav a {
  color: #000;
  font-size: 12px;
  line-height: 1.2;
  text-decoration: none;
}

.list__nav a:hover {
  color: #e8663c;
}

.gallery__wrap {
  display: flex;
  flex-wrap: wrap;
}

.gallery__box {
  height: 49px;
  width: 49px;
  margin: 2.5px;
  background: #f2ae9c;
  border: 1px solid #e8663c;
}

.gallery__box a {
  display: block;
  height: 100%;
}

.quote {
  background: #f5f5f5;
  margin-bottom: 70px;
  position: relative;
  display: flex;
}

.quote__symbol {
  font-family: serif;
  display: block;
  font-size: 68px;
  color: #e8663c;
  line-height: 0.285;
  text-align: left;
  position: relative;
  top: 30px;
  left: 10px;
}

.quote__text {
  font-size: 12px;
  padding: 25px 5px 20px 25px;
}

.share-title {
  margin: 0px 0px 10px 0px;
}

.post__share p {
  margin-top: 10px;
  font-size: 12px;
}

.switch {
  display: inline-block;
  position: relative;
  left: 17%;
  margin-bottom: 30px;
}

.switch a {
  background-color: #f2ae9c;
  width: 26px;
  height: 26px;
  display: inline-block;
  line-height: 26px;
  text-align: center;
  text-decoration: none;
  color: white;
  margin-right: 3px;
}

.switch a.is-active,
.switch a:hover {
  background: #e8663c;
}

#sidebar {
  position: fixed;
  width: 200px;
  height: 100%;
  background: #f2ae9c;
  right: -200px;
  -webkit-transition: all 500ms linear;
  transition: all 500ms linear;
  z-index: 2;
  text-align: center;
}

@media only screen and (min-width: 670px) {
  #sidebar {
    display: none;
  }
}

#sidebar.active {
  right: 0px;
}

#sidebar ul {
  list-style: none;
}

#sidebar li a {
  color: #fff;
  list-style: none;
  text-decoration: none;
  padding: 15px 10px;
  display: inline-block;
  font-weight: bold;
  text-transform: uppercase;
}

#sidebar li a.is-active,
#sidebar ul li a:hover {
  color: #e8663c;
}

#sidebar .toggle-btn {
  position: absolute;
  right: 230px;
  top: 25px;
  cursor: pointer;
}

@media only screen and (min-width: 670px) {
  #sidebar .toggle-btn {
    display: none;
  }
}

#sidebar .toggle-btn span {
  display: block;
  width: 30px;
  height: 5px;
  background: #e8663c;
  margin: 5px 0px;
}
